/* Animación de entrada (fade-in + movimiento de abajo hacia arriba) */
.fade-in {
    animation: fadeInUp 0.6s ease forwards;
  }
  
  /* Animación de salida (fade-out) */
  .fade-out {
    animation: fadeOut 0.6s ease forwards;
  }
  
  @keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes fadeOut {
    0% {
      opacity: 1;
      transform: translateY(0);
    }
    100% {
      opacity: 0;
      transform: translateY(-20px);
    }
  }


  .calendly-inline-widget {
    width: 100%;
    height: 500px;
    border: 4px solid red;
    min-height: 920px;
    min-width: auto;
    border-radius: 24px;
    overflow: hidden;
 
  }