.modal-overlay {
    position: fixed;
    z-index: 99999999;
    opacity: 1;
    visibility: visible;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
}

.modal-content {
    position: fixed;
    bottom: 0px;
    background-color: var(--primary-bg-color);
    color: var(--primary-text-color);
    max-width: 856px;
    height: 90vh;
    z-index: 100000000;
    border-radius: 24px 24px 0px 0px;
    visibility: visible;
    padding: 0px 128px 0px 128px;
}


/* Botón de cerrar */
.modal-close-btn {
    position: absolute;
    top: 48px;
    right: 48px;
    border-radius: 100px;
    border: none;
    font-size: 20px;
    cursor: pointer;
    color: var(--primary-text-color);
}

.modal-close-btn:hover {
    color: black;
}


@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.modal-content {
    animation: fadeIn 0.4s ease forwards;
}


@media (max-width:980px) {

    .modal-content {
        padding: 48px 24px 0px 24px;
        position: relative;
        height: 90vh;
    }

    .modal-close-btn {
        position: absolute;
        top: 12px;
        right: 24px;
    }

}